.all {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.plus {
    border: 2px solid teal;
    border-radius: 15px;
    background-color: green;
}

.minus {
    border: 2px solid rgb(143, 43, 43);
    border-radius: 15px;
    background-color: red;
}

.minus:hover {
    background-color: rgb(180, 52, 52);
}

.plus:hover {
    background-color: greenyellow;
}

.razdel {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: gray;
    width: 100%;
    height: 20px;
}

/* App.css или другой файл стилей */

/* Контейнер для центровки */
.centeredContainer {
    display: flex; /* Используем Flexbox */
    justify-content: center; /* Центрируем по горизонтали */
    align-items: center; /* Центрируем по вертикали */
}

/* Стиль для кнопки */
.button {
    background-color: #4CAF50; /* Зеленый цвет фона */
    color: white; /* Белый текст */
    border: none; /* Убираем границу */
    padding: 15px 32px; /* Внутренние отступы */
    text-align: center; /* Выравнивание текста по центру */
    text-decoration: none; /* Убираем подчеркивание */
    display: inline-block; /* Размещаем кнопки рядом друг с другом */
    font-size: 16px; /* Размер шрифта */
    margin: 4px 2px; /* Отступы между кнопками */
    cursor: pointer; /* Курсор при наведении */
    border-radius: 5px; /* Скругленные углы */
    transition: background-color 0.3s, transform 0.1s; /* Плавный переход для эффектов */
}

.button:hover {
    background-color: #45a049; /* Более темный зеленый при наведении */
}

.button:active {
    transform: scale(0.95); /* Уменьшение кнопки при нажатии */
}
