.modal {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed; /* Зафиксируем модальное окно на экране */
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; /* Полная высота экрана */
    background-color: rgba(0, 0, 0, 0.5); /* Прозрачное затемнение фона */
    z-index: 1000; /* Выводим модальное окно на передний план */
}

.modal-closed {
    background-color: black;
    border-radius: 500%;
    color: white;
    justify-content: center;
    align-items: center;
}

.onModal {
    display: inline-flex;
    background-color: rgb(204, 176, 176);
    width: 100%;
    justify-content: center;
    align-items: center;
    padding: 52px; /* Добавил отступы для кнопки */
    border: none; /* Убрал рамку */
    border-radius: 15px;
}

.modal-content {
    background-color: white; /* Белый фон для контента */
    padding: 20px;
    border-radius: 10px; /* Закругление углов */
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5); /* Добавляем тень */
    min-width: 300px; /* Минимальная ширина модального окна */
    text-align: center;

    /* Анимация */
    opacity: 0; /* Начальная непрозрачность */
    transform: scale(0.8); /* Начальное положение уменьшено */
    transition: opacity 0.3s ease, transform 0.3s ease; /* Параметры анимации */
}

.modal-content.show {
    opacity: 1; /* Конечная непрозрачность */
    transform: scale(1); /* Конечное положение */
}

/* Анимация появления */
@keyframes modalFadeIn {
    from {
        opacity: 0;
        transform: scale(0.8); /* Уменьшенный масштаб */
    }
    to {
        opacity: 1;
        transform: scale(1); /* Полный масштаб */
    }
}

.modal-content.show {
    animation: modalFadeIn 0.3s forwards; /* Применение анимации */
}
